import { Component, OnInit, ViewChild, ElementRef, QueryList, ViewChildren } from '@angular/core';
import { Article } from '../article';
import { RequestsService } from '../../../../services/requests-service.service';
import { MatTable, MatTableDataSource, MatTableModule, MatDialog, } from '@angular/material';
import { InfoDialogComponent } from '../../../dialog-templates/info-dialog/info-dialog.component';
import { ArticleDialogComponent } from '../dialogs/article-dialog/article-dialog.component';
import { SureDialogComponent } from '../../../dialog-templates/sure-dialog/sure-dialog.component';

@Component({
  selector: 'app-articles-page',
  templateUrl: './articles-page.component.html',
  styleUrls: ['./articles-page.component.sass'],
})
export class ArticlesPageComponent implements OnInit {

  loading: boolean;
  articles: Article[] = [];

  displayedColumns = ['id', 'name', 'actions']
  source = new MatTableDataSource();

  constructor(private requsetsService: RequestsService, private dialog: MatDialog) { }

  async ngOnInit() {
    this.loadArticles();
  }

  async loadArticles() {
    this.loading = true;
    try {
      this.articles = await this.requsetsService.SendRequest<Article[]>('GET', 'lib/articles/all', null);
      this.source = new MatTableDataSource(this.articles);
    } catch{
      this.dialog.open(InfoDialogComponent, { data: { message: 'failed to load articles' } });
    } finally {
      this.loading = false;
    }
  }

  async addArticle() {
    let newArticle = await this.dialog.open(ArticleDialogComponent, { data: { article: new Article(), action: 'Create article' } }).afterClosed().toPromise();
    let articleToAdd = await this.requsetsService.SendRequest<Article>('POST', 'lib/articles/add', newArticle);
    this.articles.push(articleToAdd);
    this.source = new MatTableDataSource(this.articles);
  }

  async editArticle(article) {
    await this.dialog.open(ArticleDialogComponent, { data: { article: article, action: 'Edit article' } }).afterClosed().toPromise();
    await this.requsetsService.SendRequest<Article>('PUT', `lib/articles/${article.id}`, article);
    this.source = new MatTableDataSource(this.articles);
  }

  async deleteArticle(article: Article) {

    if (!(await this.dialog.open(SureDialogComponent, { data: { message: 'Delete article?' } }).afterClosed().toPromise())) {
      return;
    }

    await this.requsetsService.SendRequest<any>('DELETE', `lib/articles/${article.id}`);

    this.articles = this.articles.filter(x => x.id !== article.id);
    this.source = new MatTableDataSource(this.articles);
  }
}
