import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Balance } from '../../balance';

@Component({
  selector: 'app-balance-dialog',
  templateUrl: './balance-dialog.component.html',
  styleUrls: ['./balance-dialog.component.sass']
})
export class BalanceDialogComponent implements OnInit {
  action: string;
  balance: Balance = new Balance();

  constructor(private dialogRef: MatDialogRef<BalanceDialogComponent>, @Inject(MAT_DIALOG_DATA) private data: any) {
    this.balance = data.balance;
    this.action = data.action;
  }

  ngOnInit() {
  }

  onSubmit(e) {
    e.preventDefault();
    this.dialogRef.close(this.balance);
  }
}
