import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LoginPageComponent } from './components/auth/login-page/login-page.component';
import { ArticlesPageComponent } from './components/main/articles/articles-page/articles-page.component';
import { AuthorizedGuard } from './guards/authorized.guard';

import { LayoutComponent } from './components/main/layout/layout.component'
import { BalancePageComponent } from './components/main/balance/balance-page/balance-page.component';
import { OperationPageComponent } from './components/main/operation/operation-page/operation-page.component';

const routes: Routes = [
  { path: 'auth', children: [{ path: 'login', component: LoginPageComponent }] },
  {
    path: '', component: LayoutComponent, children: [
      { path: '', redirectTo: '/articles', pathMatch: 'full' },
      { path: 'articles', component: ArticlesPageComponent, canActivate: [AuthorizedGuard] },
      { path: 'balance', component: BalancePageComponent, canActivate: [AuthorizedGuard] },
      { path: 'operation', component: OperationPageComponent, canActivate: [AuthorizedGuard] }
    ]
  }];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
