import { Component, OnInit } from '@angular/core';
import { MatTableDataSource, MatDialog } from '@angular/material';
import { InfoDialogComponent } from '../../../dialog-templates/info-dialog/info-dialog.component';
import { Operation } from '../operation';
import { SureDialogComponent } from '../../../dialog-templates/sure-dialog/sure-dialog.component';
import { RequestsService } from '../../../../services/requests-service.service';
import { OperationDialogComponent } from '../dialogs/operation-dialog/operation-dialog.component';

@Component({
  selector: 'app-operation-page',
  templateUrl: './operation-page.component.html',
  styleUrls: ['./operation-page.component.sass']
})
export class OperationPageComponent implements OnInit {

  loading: boolean;
  operations: Operation[] = [];

  displayedColumns = ['id', 'createDate', 'debit', 'credit', 'article', 'balance', 'actions']
  source = new MatTableDataSource();

  constructor(private requsetsService: RequestsService, private dialog: MatDialog) { }

  async ngOnInit() {
    this.loadOperations();
  }

  async loadOperations() {
    this.loading = true;
    try {
      this.operations = await this.requsetsService.SendRequest<Operation[]>('GET', 'lib/operations/all', null);
      this.source = new MatTableDataSource(this.operations);
    } catch{
      this.dialog.open(InfoDialogComponent, { data: { message: 'failed to load operations' } });
    } finally {
      this.loading = false;
    }
  }

  async addOperation() {
    let newoperation = await this.dialog.open(OperationDialogComponent, { data: { operation: new Operation() , action: 'Create operation' } }).afterClosed().toPromise();
    let operationToAdd = await this.requsetsService.SendRequest<Operation>('POST', 'lib/operations/add', newoperation);
    this.operations.push(operationToAdd);
    this.source = new MatTableDataSource(this.operations);
  }

  async editOperation(operation) {
    await this.dialog.open(OperationDialogComponent, { data: { operation: operation, action: 'Edit operation' } }).afterClosed().toPromise();
    await this.requsetsService.SendRequest<Operation>('PUT', `lib/operations/${operation.id}`, operation);
    this.source = new MatTableDataSource(this.operations);
  }

  async deleteOperation(operation: Operation) {

    if (!(await this.dialog.open(SureDialogComponent, { data: { message: 'Delete operation?' } }).afterClosed().toPromise())) {
      return;
    }

    await this.requsetsService.SendRequest<any>('DELETE', `lib/operations/${operation.id}`);

    this.operations = this.operations.filter(x => x.id !== operation.id);
    this.source = new MatTableDataSource(this.operations);
  }

}
