import { Component, OnInit, Input } from '@angular/core';
import { RequestsService } from '../../../services/requests-service.service';

import { MatDialog, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { InfoDialogComponent } from '../../dialog-templates/info-dialog/info-dialog.component';

@Component({
  selector: 'app-login-page',
  templateUrl: './login-page.component.html',
  styleUrls: ['./login-page.component.sass']
})
export class LoginPageComponent implements OnInit {

  constructor(private authService: RequestsService, private dialog: MatDialog, private router: Router) { }

  public form: LoginForm = new LoginForm();

  ngOnInit() {
  }

  async sendLoginRequest(e) {
    e.preventDefault();

    try {
      var result = await this.authService.SendRequest<any>("POST", "auth/signin", { userName: this.form.login, password: this.form.password });
      this.authService.SetAuthToken(result.token);

      this.router.navigateByUrl('/');
    } catch{
      this.dialog.open(InfoDialogComponent, { data: { message: 'wrong username or password' } });
    }
  }
}


class LoginForm {
  public login: string = "";
  public password: string = "";
}