import { Component, OnInit, Inject } from '@angular/core';
import { Article } from '../../../articles/article';
import { Balance } from '../../../balance/balance';
import { Operation } from '../../operation';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { RequestsService } from '../../../../../services/requests-service.service';

@Component({
  selector: 'app-operation-dialog',
  templateUrl: './operation-dialog.component.html',
  styleUrls: ['./operation-dialog.component.sass']
})
export class OperationDialogComponent implements OnInit {

  public action: string;

  public selectedArticle: Article = null;
  public selectedBalance: Balance = null;

  public articles: Article[];
  public balances: Balance[];

  public operation: Operation;

  constructor(private requsetsService: RequestsService, private dialogRef: MatDialogRef<OperationDialogComponent>, @Inject(MAT_DIALOG_DATA) private data: any) {
    this.action = data.action;
    this.operation = data.operation;
  }

  async ngOnInit() {
    this.articles = await this.requsetsService.SendRequest<Article[]>('GET', 'lib/articles/all', null);
    this.balances = await this.requsetsService.SendRequest<Balance[]>('GET', 'lib/balance/all', null);
  }

  onSubmit(e){
    e.preventDefault();
    this.dialogRef.close(this.operation);
  }

}
