import { Component, OnInit } from '@angular/core';
import { Balance } from '../balance';
import { RequestsService } from '../../../../services/requests-service.service';
import { MatTable, MatTableDataSource, MatTableModule, MatDialog, } from '@angular/material';
import { InfoDialogComponent } from '../../../dialog-templates/info-dialog/info-dialog.component';
import { BalanceDialogComponent } from '../dialogs/balance-dialog/balance-dialog.component';
import { SureDialogComponent } from '../../../dialog-templates/sure-dialog/sure-dialog.component';

@Component({
  selector: 'app-balance-page',
  templateUrl: './balance-page.component.html',
  styleUrls: ['./balance-page.component.sass']
})
export class BalancePageComponent implements OnInit {
  loading: boolean;
  Balances: Balance[] = [];

  displayedColumns = ['id', 'credit', 'debit', 'amount', 'createdDate', 'actions']
  source = new MatTableDataSource();

  constructor(private requsetsService: RequestsService, private dialog: MatDialog) { }

  async ngOnInit() {
    this.loadBalances();
  }

  async loadBalances() {
    this.loading = true;
    try {
      this.Balances = await this.requsetsService.SendRequest<Balance[]>('GET', 'lib/balance/all', null);
      this.source = new MatTableDataSource(this.Balances);
    } catch{
      this.dialog.open(InfoDialogComponent, { data: { message: 'failed to load  Balances' } });
    } finally {
      this.loading = false;
    }
  }

  async addBalance() {
    let newBalance = await this.dialog.open(BalanceDialogComponent, { data: { balance: new Balance(), action: 'Create  Balance' } }).afterClosed().toPromise();
    newBalance.createDate = new Date();
    let BalanceToAdd = await this.requsetsService.SendRequest<Balance>('POST', 'lib/balance/add', newBalance);
    this.Balances.push(BalanceToAdd);
    this.source = new MatTableDataSource(this.Balances);
  }

  async editBalance(Balance) {
    await this.dialog.open(BalanceDialogComponent, { data: { balance: Balance, action: 'Edit  Balance' } }).afterClosed().toPromise();
    await this.requsetsService.SendRequest<Balance>('PUT', `lib/balance/${Balance.id}`, Balance);
    this.source = new MatTableDataSource(this.Balances);
  }

  async deleteBalance(Balance: Balance) {

    if (!(await this.dialog.open(SureDialogComponent, { data: { message: 'Delete Balance?' } }).afterClosed().toPromise())) {
      return;
    }

    await this.requsetsService.SendRequest<any>('DELETE', `lib/balance/${Balance.id}`);

    this.Balances = this.Balances.filter(x => x.id !== Balance.id);
    this.source = new MatTableDataSource(this.Balances);
  }
}
